body{
  background-color:aliceblue;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  font-family: 'Poppins',sans-serif;
  background-image: url('C:/Users/Lenovo/Desktop/Internship/weather-app/Images/weather_bg.jpg');
  background-repeat: no-repeat;
  width: 100%;
  background-size: cover;
}

.app
{
  width: 400px;
  padding: 25px;
  color:#e5e5e5;
  border-radius: 10px;
  box-shadow: 4px 4px 4px 15px 0px rgba(0,0,0,0.2);
  background:linear-gradient(108deg,rgba(128,0,128,0.9),rgba(128,0,128,0.5));
}

.app h1{
  text-align: center;
  font-size:24px;
  font-weight:400;
}

.input-wrapper
{
  display: flex;
}

.input-wrapper input{
  width:100%;
  padding: 5px 10px;
  height: 30px;
  font-size:14px;
  border:none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.input-wrapper input:focus{
  outline:none;
}
.input-wrapper button
{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  border: none;
  color: #fff;
  border-top-right-radius: 5px;
  border-bottom-right-radius:5px;
  cursor:pointer;
  width:80px;
}

.content
{
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(262deg, rgba(0,0,0,0.1),rgba(0,0,0,0.4));
  margin-top: 10px;
  border-radius: 10px;
}

.location
{
  margin-top: 20px;
}

.location h2
{
  margin: 0;
  margin-left: 10px;
  font-weight: 300;
}
.location h2 span
{
  font-size: 14px;
}
.d-flex
{
  display:flex;
  align-items:center;
  justify-content: center;
}


.datetext
{
  margin-top: 10px;
 font-size: 14px;
}

.weatherdesc h3{
  margin: 0;
  text-transform: capitalize;
  font-weight:400px;
}

.tempstats
{
  width: 100%;
  padding: 20px 0px;
}

.tempstats h1
{
  font-size: 64px;
  font-weight: 200;
  margin:0;
}

.tempstats h1 span
{
  font-size: 38px;
}

.tempstats h3
{
  font-weight: 400;
}

.flex-c
{
  flex-direction: column;
}

.windstats
{
  padding: 0px 20px;
  background: rgba(0,0,0,0.3);
  margin-bottom: 20px;
  border-radius: 10px;
}

.windstats h3
{
  margin-left:10px;
  font-weight: 400;
  font-size:large;
}